import { Union, Record } from "./.fable/fable-library.3.0.5/Types.js";
import { parseUrl, defaultPage, Page$reflection } from "./Router.js";
import { union_type, record_type } from "./.fable/fable-library.3.0.5/Reflection.js";
import { RouterModule_urlSegments } from "./.fable/Feliz.Router.3.5.0/Router.fs.js";
import { Cmd_none } from "./.fable/Fable.Elmish.3.1.0/cmd.fs.js";

export class Model extends Record {
    constructor(CurrentPage) {
        super();
        this.CurrentPage = CurrentPage;
    }
}

export function Model$reflection() {
    return record_type("Docs.State.Model", [], Model, () => [["CurrentPage", Page$reflection()]]);
}

export const ModelModule_init = new Model(defaultPage);

export class Msg extends Union {
    constructor(tag, ...fields) {
        super();
        this.tag = (tag | 0);
        this.fields = fields;
    }
    cases() {
        return ["UrlChanged"];
    }
}

export function Msg$reflection() {
    return union_type("Docs.State.Msg", [], Msg, () => [[["Item", Page$reflection()]]]);
}

export function init() {
    return [new Model(parseUrl(RouterModule_urlSegments(window.location.hash, 1))), Cmd_none()];
}

export function update(msg, currentModel) {
    return [new Model(msg.fields[0]), Cmd_none()];
}

